body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    margin-bottom: 30px;
    padding: 20px;
    background-color: #5133a5;
  }

  .navbar h1 {
      font-size: 100%;
      font-weight: 650;
  }

  .navbar h2 {
    font-size: 90%;
    font-weight: 575;
  }

  .score-display {
      font-size: 100%;
      font-weight: 400;
  }

  .highscore-display {
      color: rgb(0, 255, 0);
  }

  @media only screen and (min-width: 980px) {
    .navbar h1 {
        font-size: 270%;
        font-weight: 650;
    }
  
    .navbar h2 {
      font-size: 225%;
      font-weight: 575;
    }
  
    .score-display {
        font-size: 250%;
        font-weight: 400;
    }
  }

  @media only screen and (min-width: 798px) {
    .navbar h1 {
        font-size: 150%;
        font-weight: 650;
    }
  
    .navbar h2 {
      font-size: 130%;
      font-weight: 575;
    }
  
    .score-display {
        font-size: 150%;
        font-weight: 400;
    }
  }

 .clicky-game-header {
    height: 280px;
    padding-top: 120px;
    background-image: url(/react-clicky-game/static/media/header-background.061e75ec.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    color: #FFFFFF;
    text-align: center;
    font-size: 110%;
    margin-bottom: 10px;
  }
  
  .clicky-game-header h2 {
    font-size: 250%;
  } 

  @media only screen and (min-width: 980px) {
    .clicky-game-header {
      height: 400px;
      padding-top: 200px;
      font-size: 150%;
    }
  }
.card {
    background-repeat: no-repeat;
    background-size: 70%;
    background-position: center;
    width: 100%;
    height: 120px;
    margin: 5px auto;
  }
  
  .card:hover {
      -webkit-animation-name: image-bulge;
              animation-name: image-bulge;
      -webkit-animation-duration: 0.25s;
              animation-duration: 0.25s;
      -webkit-animation-fill-mode: forwards;
              animation-fill-mode: forwards; 
      cursor: pointer;
  }
  
  @-webkit-keyframes image-bulge {
      0% {-webkit-transform: scale(1,1);transform: scale(1,1);}
      100% {-webkit-transform: scale(1.01,1.01);transform: scale(1.01,1.01); box-shadow: 1px 1px 30px #666666;}
  }
  
  @keyframes image-bulge {
      0% {-webkit-transform: scale(1,1);transform: scale(1,1);}
      100% {-webkit-transform: scale(1.01,1.01);transform: scale(1.01,1.01); box-shadow: 1px 1px 30px #666666;}
  }

  @media only screen and (min-width: 798px) {
      .card {
          height: 175px;
          margin: 15px auto;
      }
  }
